/* global google */
import { errorMessage } from "@/services/MessageService";
/**
 * Search address suggestions to show into an autocpmplete field
 * @param {*} searchText search text typed by user in the autocomplete field
 */
const GetSuggestions = async (searchText) => {
  let result;
  try {
    const rawResult = await searchLocation(searchText);
    result = rawResult.map((res) => {
      return {
        id: res.place_id,
        value: res.description,
      };
    });
  } catch (err) {
    result;
  }
  return result;
};
/**
 * Resolve suggestions array from google places
 * @param {*} val search text typed by user in the autocomplete field
 */
const searchLocation = async (val) => {
  let promise = await new Promise((resolve, reject) => {
    var displaySuggestions = (predictions, status) => {
      if (status !== google.maps.places.PlacesServiceStatus.OK) {
        reject(status);
      }
      resolve(predictions);
    };
    var service = new google.maps.places.AutocompleteService();

    if (val.length > 0) {
      service.getPlacePredictions(
        {
          input: val,
          types: ["geocode"],
        },
        displaySuggestions
      );
    }
  }).catch(function(err) {
    throw err;
  });
  return promise;
};
/**
 * Get the latitude and longitude from an address string
 * @param {*} address Address string coming from places library
 */
const GetLocation = async (address) => {
  let promise = await new Promise((resolve, reject) => {
    const geocoder = new google.maps.Geocoder();
    geocoder.geocode({ address: address }, (results, status) => {
      if (status === "OK") {
        resolve(results[0].geometry.location);
      } else {
        reject(status);
      }
    });
  });
  return promise;
};

const GetAddressByLatLng = async (lat_lng) => {
  const geocoder = new google.maps.Geocoder();
  let promise = await new Promise((resolve, reject) => {
    geocoder.geocode({ location: lat_lng }, (results, status) => {
      if (status === "OK") {
        if (results[0]) {
          resolve(results[0]);
        }
      } else {
        reject(status);
      }
    });
  });
  return promise;
};
/**
 * Draws the polygon shape inside the map
 * @param {*} paths Array of polygon points to be displayed in map
 * @param {*} mapElement Map promise reference
 */
const DrawPolygon = async (paths, mapElement) => {
  // :: Refactor polygon array that comes from corriendito REST
  // :: because values are named in a different way that google expects
  var redifinedPaths = [];
  paths.forEach((path) => {
    redifinedPaths.push({
      lat: path.latitude,
      lng: path.longitude,
    });
  });
  // :: Construct the polygon.
  var storageCoverage = new google.maps.Polygon({
    paths: redifinedPaths,
    strokeColor: "#FF0000",
    strokeOpacity: 0.8,
    strokeWeight: 2,
    fillColor: "#FF0000",
    fillOpacity: 0.35,
  });
  // :: Set the polygon form into the referred map
  storageCoverage.setMap(mapElement);
};

/**
 * Draws the route to obtain the km between address and store
 * @param {*} coordenatesAddress object with the coordenates of the user address
 */
const DrawRoute = async (coordenatesAddress, coordenatesStore) => {
  // :: var address and store with long and lat
  var address = new google.maps.LatLng(
    coordenatesAddress.lat,
    coordenatesAddress.long
  );
  var store = new google.maps.LatLng(
    coordenatesStore.lat,
    coordenatesStore.long
  );
  var directionsService = new google.maps.DirectionsService();
  const mode = "DRIVING";
  let result;
  var request = {
    origin: store,
    destination: address,
    travelMode: mode,
  };
  let promise = await new Promise((resolve, reject) => {
    directionsService.route(request, function(response, status) {
      if (status == google.maps.DirectionsStatus.OK) {
        resolve(response.routes[0].legs[0].distance);
      } else {
        reject(status);
      }
    });
  });
  return promise;
};
export {
  GetSuggestions,
  GetLocation,
  GetAddressByLatLng,
  DrawPolygon,
  DrawRoute,
};
