<template>
  <v-row class="mx-auto">
    <v-form class="form--content" ref="form" v-model="valid">
      <v-col cols="12" lg="8" md="8">
        <v-row>
          <v-col cols="12" lg="6" md="6">
            <span>1.- Datos de Entrega</span>
            <v-card class="mt-1">
              <v-card-text class="pb-0">Dirección: {{ data.addressSelected.addressName}}</v-card-text>
              <v-card-text class="pt-0">Teléfono: +{{ data.phone_number}}</v-card-text>
            </v-card>
          </v-col>
          <v-col cols="12" lg="6" md="6">
            <span>2.- Método de pago</span>
            <v-autocomplete
              class="mt-1"
              v-model="paymentMethodModel"
              :items="paymentMethods"
              :rules="dataRules"
              item-text="name"
              item-value="id"
              solo
              label="Método de pago"
              required
              hide-selected
              hide-details="auto"
              @change="changePaymentMethod"
            >
              <template v-slot:item="{ item }">
                <v-list-item-content>
                  <v-list-item-title v-text="item.name"></v-list-item-title>
                </v-list-item-content>
              </template>
            </v-autocomplete>
          </v-col>
          <v-col v-if="showQuantityCash">
            <span v-if="showQuantityCash" >2.1 - Cantidad en efectivo a pagar</span>
            <v-text-field
            v-if="showQuantityCash"
                  v-model="cashAmount"
                  label="Precio"
                  :rules="showQuantityCash ? dataRules : []"
                  required
                  solo
                  prefix="$"
                  hide-details="auto"
                  v-currency="{autoDecimalMode: true}"
            ></v-text-field>
          </v-col>
          <v-col cols="12" lg="6" md="6">
            <span>3.- Horario de entrega</span>
            <v-autocomplete
              class="mt-1"
              v-model="selectedHour"
              :items="pickupMethods"
              :rules="dataRules"
              item-text="TIME"
              item-value="id"
              solo
              label="Horario de entrega"
              required
              hide-selected
              hide-details="auto"
            >
              <template v-slot:item="{ item }">
                <v-list-item-content>
                  <v-list-item-title v-text="item.TIME"></v-list-item-title>
                </v-list-item-content>
              </template>
            </v-autocomplete>
          </v-col>
          <v-col cols="12" lg="6" md="6">
            <span>4.- Tipo de entrega</span>
            <v-autocomplete
              class="mt-1"
              v-model="selectedDeliveryType"
              :items="deliveryType"
              :rules="dataRules"
              item-text="type"
              item-value="id"
              solo
              label="Tipo de entrega"
              required
              hide-selected
              hide-details="auto"
            >
              <template v-slot:item="{ item }">
                <v-list-item-content>
                  <v-list-item-title v-text="item.type"></v-list-item-title>
                </v-list-item-content>
              </template>
            </v-autocomplete>
          </v-col>
          <v-col cols="12">
            <span>5.- Comentarios</span>
            <v-textarea v-model="comments" placeholder="Comentarios" solo rows="3" row-height="10" hide-details="auto"></v-textarea>
          </v-col>
        </v-row>
      </v-col>
      <v-col cols="12" lg="4" md="4">
        <span>6.- Confirmación de pedido</span>
        <v-card class="mt-1">
          <v-card-text>Subtotal: ${{this.subtotal.toFixed(2)}}</v-card-text>
          <v-card-text>Costo de envio: ${{shiptotal.toFixed(2)}}</v-card-text>
          <v-card-text>Total: {{totalPrice}}</v-card-text>
          <v-card-actions>
              <v-btn color="primary" @click="finishPurchase" block> confirmar pedido </v-btn>
          </v-card-actions>
        </v-card>
      </v-col>
    </v-form>
    <GmapMap
          class="d-none"
          ref="mapRef"
          :center="center"
          :zoom="10"
          map-type-id="terrain"
          style="width: 100%; height: 500px"
        >
        </GmapMap>
  </v-row>
</template>
<script>
import {
  DrawRoute
} from "@/services/MapsPlacesAutocomplete.js";
import { mapActions, mapState, mapGetters } from "vuex";
export default {
  computed: {
    ...mapState("checkout", ["dataCheckout", "paymentMethods", "pickupMethods", "subtotal"]),
    ...mapGetters("login", ["data", "dataStore"]),
    deliveryType() {
      var deliveryType = [];
      deliveryType.push({ id: 1, type: "Entrega a domicilio" });
      deliveryType.push({ id: 2, type: "Recoge en tienda" });

      return deliveryType;
    },
    totalPrice() {
        this.total = 0
        const kilometros = this.distance.value/1000;
        this.shiptotal = this.dataStore.delivery_price * kilometros
        this.total = this.subtotal + this.shiptotal
        return this.total.toFixed(2)
    }
  },
 async mounted() {
    this.getPaymentMethods();
    this.getTimeOptions();
    const map = await this.$refs.mapRef.$mapPromise;
    const coordenatesStore = {
        lat: this.dataStore.latitude,
        long: this.dataStore.longitude
    }
    const coordenatesAddress = {
        lat: this.data.addressSelected.addressLatitude,
        long: this.data.addressSelected.addressLongitude
    }
    this.distance = await DrawRoute(coordenatesAddress, coordenatesStore)
  },
  data() {
    return {
      valid: false,
      paymentMethodModel: null,
      selectedHour: null,
      selectedDeliveryType: null,
      comments: null,
      cashAmount: null,
      shiptotal: 0,
      total: 0,
      showQuantityCash: false,
      dataPurchaseObject: {},
      dataRules: [v => !!v || "El dato es requerido para continuar"],
      center: {
        lat: 17.98398998452883,
        lng: -92.93958413281251,
      },
      distance: {
          text: '',
          value: 0
      }
    };
  },
  methods: {
    ...mapActions("checkout", ["postCheckoutProcess", "getPaymentMethods", "getTimeOptions"]),
    changePaymentMethod() {
        if (this.paymentMethodModel == 1) {
            this.cashAmount = 0
            this.showQuantityCash = true
        } else {
            this.cashAmount = 0
            this.showQuantityCash = false
        }
    },
    finishPurchase() {
        if (this.$refs.form.validate()){ 
            this.dataPurchaseObject.address = parseInt(this.data.addressSelected.addressId);  
            this.dataPurchaseObject.payment_method = this.paymentMethodModel
            this.dataPurchaseObject.cash_amount = parseFloat(this.cashAmount)
            this.dataPurchaseObject.pickup_time_id = this.selectedHour
            this.dataPurchaseObject.delivery_type_id = this.selectedDeliveryType
            this.dataPurchaseObject.delivery_distance = this.selectedDeliveryType == '1' ? this.distance.value/1000 : null
            this.dataPurchaseObject.delivery_price = this.selectedDeliveryType == '1' ? this.shiptotal : null
            this.dataPurchaseObject.comments = this.comments

            this.postCheckoutProcess(this.dataPurchaseObject)
        }
    }
  }
};
</script>
<style lang="scss">
.form--content {
  display: block;

  @media only screen and (min-device-width: 960px) {
    display: inline-flex;
  }
}
</style>